/**
 * Used for preparing editir.js object for both New and Exitsing post
 * @param {Data} data Data from existing blogs for updating.
 */
export function editor(data) {
    const editorjs = new EditorJS({
        holder: 'editor',
        autofocus: true,
        placeholder: "Let`s write an awesome prompt here!",
        header: {
            class: Header, 
            inlineToolbar: ['link'] 
          }, 
        list: { 
            class: List, 
            inlineToolbar: true 
        },
        tools: {
            image: {
                class: ImageTool,
                config: {
                    endpoints: {
                        byFile: 'http://localhost:3001/saveImage',
                        byUrl: 'data:image/jpeg;base64,'
                    }
                }
            },
            embed: Embed,
            // table: Table,
            marker: Marker,
            list: List,
            warning: Warning,
            // code: Code,
            // linkTool: LinkTool,
            // raw: Raw,   
            header: Header,
            quote: Quote,
            // checklist: CheckList,
            delimiter: Delimiter,
            // inlineCode: InlineCode,
            simpleImage: SimpleImage
        },
        data
    })
    // if(window?.editorjs) {
    //     return window.editorjs
    // }
    return editorjs
}
